// CODE
//////////////////////////////////////

.canhcam-header-6 {
	background: color-get($mau,trang);
	border-bottom: 1px solid darken(color-get($mau,trang),20%);
	.navbar {
		.title {
			order: 0;
			display: none;
			color: color-get($mau,trang);
			padding: 1rem 2rem;
			border-bottom: 1px dashed lighten(color-get($mau,main),40%);
			h2 {
				margin-bottom: 0;
				@include fz(2rem); }
			.closebnt {
				@include pos(abs, .5rem .5rem null null);
				z-index: 999;
				color: color-get($mau,trang);
				cursor: pointer;
				i {
					font-size: 3rem;
					line-height: 3rem; } } } }
	.navbar-toggler {
		border: 1px solid darken(color-get($mau,trang),20%);
		border: 0;
		.mdi {
			font-size: 2rem; }
		&:focus {
			outline: none; } }
	.navbar-brand {
		img {
			max-height: 2rem; } }
	@include media-breakpoint-down(md) {
		.navbar-collapse {
			display: flex;
			flex-direction: column;
			@include transition(all .3s ease-in-out);
			@include pos(fix, 0 100% null null);
			background: rgba(red(color-get($mau,main)),green(color-get($mau,main)),blue(color-get($mau,main)),.95);
			height: 100%;
			width: 100%;
			overflow-y: scroll;
			display: block;
			@include pad(0);
			.search {
				order: 1;
				@include pad(0 2rem);
				button {
					background: darken(color-get($mau,trang),5%);
					color: color-get($mau,main);
					border-color: darken(color-get($mau,trang),5%); } }
			.navbar-nav {
				order: 2;
				@include pad(1rem 2rem);
				@include fz(1.3rem);
				font-weight: 300;
				.nav-link {
					@include link(color-get($mau,trang), color-get($mau,extra), color-get($mau,extra)); } } } }
	&.expand {
		.navbar {
			.title {
				display: block; } }
		.navbar-toggler {
			background: none;
			color: color-get($mau,main);
			border-color: none; }
		.navbar-collapse {
			@include pos(fix, 0 0 null null);
			z-index: 999; } }
	@include media-breakpoint-up(xl) {
		.navbar-nav {
			.nav-item {
				.nav-link {
					@include pad(.5rem 1rem); } } } }
	@include media-breakpoint-up(lg) {
		.navbar {
			position: relative; }
		.navbar-collapse {
			position: static; }
		.search {
			@include pos(abs, 100% 1rem null null);
			display: none;
			max-width: 300px;
			background: darken(color-get($mau,trang),0%);
			@include pad(.5rem);
			border: 1px solid darken(color-get($mau,trang),20%);
			border-top: 0;
			@include bor-rad(0 0 3px 3px);
			.input-group {
				width: 100%; }

			&.active {
				display: block; } }
		.btnsearch {
			.btn {
				text-decoration: none;
				i {
					width: 20px;
					height: 20px;
					line-height: 20px;
					text-align: center; } }
			.fa-times {
				display: none; }
			&.active {
				.fa-times {
					display: block; }
				.fa-search {
					display: none; } } } } }
