@import "../../_includes";
//////////////////////////////////////
// CODE
//////////////////////////////////////

.canhcam-gallery-3 {
	@include pad(2rem 0);
	@include media-breakpoint-up(lg) {
		@include pad(4rem 0); }
	.gallery-details {
		.item {
			display: flex;
			text-decoration: none;
			height: 100%;
			@include bor-rad(5px);
			overflow: hidden;
			figure {
				position: relative;
				overflow: hidden;
				@include bor-rad(5px);
				margin-bottom: 2rem;
				background: darken(color-get($mau,trang) ,3%);
				&:after {
					content: '';
					display: block;
					z-index: -1;
					background: rgba(0,0,0,.5);
					width: 100%;
					height: 100%;
					@include pos(abs, 0 0 null null); }
				.zoom-item {
					@include opacity(0);
					color: color-get($mau,trang);
					@include pos(abs, 50% null null 50%);
					@include transform(translateX(-50%) translateY(-50%));
					z-index: 10;
					@include fz(3rem);
					@include transition(all .2s ease-in-out); }
				.boxzoom {
					overflow: hidden; }
				img {
					@include bor-rad(5px 5px 0 0);
					@include transition(all .5s ease-in-out); }
				figcaption {
					padding: 1rem;

					h3 {
						@include fz(1.1rem);
						@include fw(400);
						color: color-get($mau,den);
						margin-bottom: 0; } } }
			&:hover {
				figure {
					&:after {
						z-index: 1; }
					img {
						@include transform(scale(1.1)); }
					.zoom-item {
						@include opacity(1); } } } } }
	.pagelist {
		.pagination {
			margin-bottom: 0;
			.page-item {
				margin-left: .5rem;
				&:first-child {
					margin-left: 0; }
				.page-link {
					@include bor-rad(50% 50%);
					width: 2.5rem;
					height: 2.5rem;
					justify-content: center;
					align-items: center;
					display: flex;
					color: darken(color-get($mau,trang) ,20%); }
				&.active {
					.page-link {
						color: darken(color-get($mau,trang) ,0%); } } } } } }
