@import "../../_includes";
//////////////////////////////////////
// CODE
//////////////////////////////////////

.canhcam-news-2 {
	@include pad(2rem 0);
	@include media-breakpoint-up(lg) {
		@include pad(4rem 0); }
	.news-hot {
		figure {
			position: relative;
			margin-bottom: 2rem;
			overflow: hidden;
			@include bor-rad(5px);
			img {
				width: 100%; }
			.item {
				display: block; }
			figcaption {
				@include pos(abs, 0 null 0 null);
				width: 100%;
				height: 100%;
				padding: 1rem;
				color: #fff;
				z-index: 1;
				@include text-sha(2px 2px 3px rgba(0,0,0,.2));
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				h3 {
					@include fz(1.2rem);
					@include fw(300); }
				time {
					@include fz(.9rem);
					@include fw(200);
					color: darken(color-get($mau,whites) ,10%); }
				&:after {
					content: '';
					@include pos(abs, null 0 0 null);
					width: 100%;
					height: 50%;
					z-index: -1;
					@include transition(ease-in-out all .15s);
					@include opacity(0);
					background: rgba(0,0,0,.5);
					background: -moz-linear-gradient(top, rgba(0,0,0,0) 50%, rgba(0,0,0,0.65) 100%);
					background: -webkit-linear-gradient(top, rgba(0,0,0,0) 50%,rgba(0,0,0,0.65) 100%);
					background: linear-gradient(to bottom, rgba(0,0,0,0) 50%,rgba(0,0,0,0.65) 100%);
					filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#a6000000',GradientType=0); } }
			&:hover {
				figcaption {
					&:after {
						@include opacity(1);
						height: 100%; } } } }
		.top-list {
			.item {
				@include media-breakpoint-up(lg) {
					figure {
						margin-bottom: 0; } } } }

		.part-list {
			.item {
				figure {
					figcaption {
						@include pad(.5rem 1rem);
						h3 {
							@include fz(1rem); }
						time {
							@include fz(.8rem); } } } } } }
	.news-list {
		.btn-group {
			i {
				padding-right: 5px; } }
		.list-group-wrapper {
			flex-direction: column;
			> div {
				-webkit-box-flex: 0;
				-webkit-flex: 0 0 100%;
				-ms-flex: 0 0 100%;
				flex: 0 0 100%;
				max-width: 100%;
				figure {
					display: block;
					@include media-breakpoint-up(md) {
						@include clearfix;
						img {
							max-width: 30%;
							float: left;
							@include bor-rad(5px 0 0 5px); }
						figcaption {
							max-width: 70%;
							float: left;
							padding-left: 1rem; } } } } }

		.item {
			display: flex;
			text-decoration: none;
			height: 100%;
			@include bor-rad(5px);
			overflow: hidden;
			figure {
				position: relative;
				overflow: hidden;
				@include bor-rad(5px);
				margin-bottom: 2rem;
				background: darken(color-get($mau,trang) ,3%);
				img {
					@include bor-rad(5px 5px 0 0); }
				figcaption {
					padding: 1rem;

					h3 {
						@include fz(1.1rem);
						@include fw(400);
						color: color-get($mau,main); }
					time {
						@include fz(.9rem);
						@include fw(200);
						color: darken(color-get($mau,trang) ,30%); }
					p {
						color: darken(color-get($mau,trang) ,60%);
						line-height: 1.3rem;
						@include fz(1rem);
						margin-bottom: 0; } } }
			&:hover {
				figure {
					background: darken(color-get($mau,trang) ,7%);
					img {
						@include opacity(.7); }
					figcaption {
						h3 {
							color: darken(color-get($mau,extra),0%); }
						time {
							color: darken(color-get($mau,trang) ,20%); }
						p {
							color: darken(color-get($mau,trang) ,70%); } } } } } }
	.pagelist {
		.pagination {
			margin-bottom: 0;
			.page-item {
				margin-left: .5rem;
				&:first-child {
					margin-left: 0; }
				.page-link {
					@include bor-rad(50% 50%);
					width: 2.5rem;
					height: 2.5rem;
					justify-content: center;
					align-items: center;
					display: flex;
					color: darken(color-get($mau,trang) ,20%); }
				&.active {
					.page-link {
						color: darken(color-get($mau,trang) ,0%); } } } } } }
