// CODE
//////////////////////////////////////

.canhcam-carousel-2 {
	@include pad(2rem 0);
	@include media-breakpoint-up(lg) {
		@include pad(4rem 0); }
	.owl-carousel {
		position: relative;
		// Main
		.owl-stage-outer {}
		// Nav
		.owl-nav {
			button {
				&.owl-prev, &.owl-next {
					@include pad(.5rem_);
					width: 3rem;
					height: 3rem;
					background: darken(color-get($mau,trang),5%);
					@include bor-rad(50%);
					text-align: center;
					&:focus {
						outline: none; } }
				&:hover, &.active {
					&.owl-prev, &.owl-next {
						background: color-get($mau,main); } } }
			@include media-breakpoint-up(md) {
				margin-top: 0;
				button {
					&.owl-prev, &.owl-next {
						margin: 0;
						@include transform(translateY(-50%));
						&:focus {
							outline: none; } }
					&.owl-prev {
						@include pos(abs, 50% null null -1rem); }
					&.owl-next {
						@include pos(abs, 50% -1rem null null); } } } }
		// Dots
		.owl-dots {
			.owl-dot {
				&:focus {
					outline: none; }
				span {
					background: darken(color-get($mau,trang),5%); }
				&.active {
					span {
						background: color-get($mau,main); } } } }
		// List
		&.list-items {
			.item {
				a {
					color: darken(color-get($mau,trang),50%);
					text-decoration: none;
					.price {
						color: color-get($mau,do-1);
						@include fw(bold); } }
				&:hover {
					img {
						@include opacity(.8); }
					a {
						color: color-get($mau,den); }
					h3 {
						text-decoration: underline; } } } } } }
