@import "../../_includes";
//////////////////////////////////////
// CODE
//////////////////////////////////////

.canhcam-carousel-3 {
	@include pad(2rem 0);
	@include media-breakpoint-up(lg) {
		@include pad(4rem 0); }
	.owl-carousel {
		position: relative;
		// Main
		.owl-stage-outer {
			.owl-stage {
				display: flex; }
			.owl-item {
				display: flex; } }
		// Nav
		.owl-nav {
			text-align: center;
			button {
				&.owl-prev, &.owl-next {
					@include mar(.5rem);
					@include pad(.5rem_);
					width: 3rem;
					height: 3rem;
					background: darken(color-get($mau,trang),5%);
					@include bor-rad(50%);
					text-align: center;
					&:focus {
						outline: none; } }
				&:hover, &.active {
					&.owl-prev, &.owl-next {
						background: color-get($mau,main);
						color: color-get($mau,trang); } }
				&.disabled {
					@include opacity(.5); } } }
		// Dots
		.owl-dots {
			.owl-dot {
				&:focus {
					outline: none; }
				span {
					background: darken(color-get($mau,trang),5%); }
				&.active {
					span {
						background: color-get($mau,main); } } } } }
	.news-list {
		.item {
			display: flex;
			text-decoration: none;
			height: 100%;
			@include bor-rad(5px);
			overflow: hidden;
			figure {
				position: relative;
				overflow: hidden;
				@include bor-rad(5px);
				background: darken(color-get($mau,trang) ,3%);
				img {
					@include bor-rad(5px 5px 0 0); }
				figcaption {
					padding: 1rem;

					h3 {
						@include fz(1.1rem);
						@include fw(400);
						color: color-get($mau,main); }
					time {
						@include fz(.9rem);
						@include fw(200);
						color: darken(color-get($mau,trang) ,30%); }
					p {
						color: darken(color-get($mau,trang) ,60%);
						line-height: 1.3rem;
						@include fz(1rem);
						margin-bottom: 0; } } }
			&:hover {
				figure {
					background: darken(color-get($mau,trang) ,7%);
					img {
						@include opacity(.7); }
					figcaption {
						h3 {
							color: darken(color-get($mau,extra),0%); }
						time {
							color: darken(color-get($mau,trang) ,20%); }
						p {
							color: darken(color-get($mau,trang) ,70%); } } } } } } }
