.canhcam-carousel-1 {
	@include pad(2rem 0);
	@include media-breakpoint-up(lg) {
		@include pad(4rem 0); }
	.owl-carousel {
		// Main
		.owl-stage-outer {}
		// Nav
		.owl-nav {
			button {
				&.owl-prev, &.owl-next {
					@include pad(.5rem_);
					width: 3rem;
					height: 3rem;
					background: darken(color-get($mau,trang),5%);
					@include bor-rad(50%);
					text-align: center;
					&:focus {
						outline: none; } }
				&:hover, &.active {
					&.owl-prev, &.owl-next {
						background: color-get($mau,main); } } } }
		// Dots
		.owl-dots {
			.owl-dot {
				&:focus {
					outline: none; }
				span {
					background: darken(color-get($mau,trang),5%); }
				&.active {
					span {
						background: color-get($mau,main); } } } } } }
