//////////////////////////////////////

.canhcam-footer-1 {
	background-color: darken(color-get($mau,trang) ,85%);
	position: relative;
	z-index: 1;
	color: darken(color-get($mau,trang) ,50%);
	overflow: hidden;
	@include media-breakpoint-down(xs) {
		text-align: center; }
	h4 {
		text-transform: uppercase;
		color: color-get($mau,trang);
		position: relative;
		@include fz(1rem);
		margin-bottom: 1.5rem;
		&:after {
			width: 40px;
			height: 1px;
			background: color-get($mau,main);
			content: '';
			display: block;
			@include pos(abs, null null -10px 50%);
			@include transform(translateX(-50%));
			@include media-breakpoint-up(md) {
				@include pos(abs, null null -10px 0);
				@include transform(translateX(0)); } } }
	.linkfooter {
		padding: 3rem 0 2rem;
		.list-group {
			.list-group-item {
				display: inline-block;
				@include bg(none, none, none);
				border: 0;
				@include pad(.5rem 0);
				@include media-breakpoint-up(md) {
					@include pad(.15rem 0); }
				a {
					font-size: .9rem;
					@include link(darken(color-get($mau,trang),15%),color-get($mau,main),color-get($mau,main)); } } }
		.socials {
			h4 {
				&:after {
					@include media-breakpoint-up(md) {
						@include pos(abs, null null -10px 50%);
						@include transform(translateX(-50%)); }
					@include media-breakpoint-up(lg) {
						@include pos(abs, null null -10px 0);
						@include transform(translateX(0)); } } }
			.social-list {
				@include media-breakpoint-down(sm) {
					@include mar(2rem null null null); }
				.list-inline {
					@include mar(0);
					.list-inline-item {
						@include fz(.9rem);
						@include link(darken(color-get($mau,trang), 85%), color-get($mau,trang), color-get($mau,trang));
						@include mar(null 1rem null null);
						a {
							display: block;
							height: 2rem;
							width: 2rem;
							@include lh(2rem);
							@include bg(darken(color-get($mau,trang),40%), color-get($mau,main), color-get($mau,main));
							@include link(darken(color-get($mau,trang),85%), darken(color-get($mau,trang),0%));
							@include border-radius(50%);
							text-align: center;
							i {
								@include lh(2rem); } }
						&:last-child {
							@include mar(0); } } } } } }
	.copyright {
		padding: 1rem;
		background-color: darken(color-get($mau,trang) ,95%); } }
